*{
    margin:0;
    padding:0;
}

.Home{
    width:100vw;
    padding:1cm;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.unlist{
    list-style: none;
}

.inputText{
    width:10cm;
}

.list{
    display: flex;
    flex-direction: row;
    column-gap: 0.2cm;
    padding: 0.2cm;
    border: thin solid rgb(197, 197, 197);
    border-radius: 6px;
}

.delete{
    width:1cm;
    height:1cm;
}